<template>
  <div>
    <el-row :gutter="10">
      <el-input v-model="orderId" placeholder=""></el-input>
      <el-input v-model="sourceName" placeholder=""></el-input>
      <el-button type="primary" icon="el-icon-search" @click="search"></el-button>
    </el-row>
    <hr>
    <el-table :data="tableData">
      <el-table-column prop="id" label=""></el-table-column>
      <el-table-column :formatter="formatType" label=""></el-table-column>
      <el-table-column prop="sourceName" label=""></el-table-column>
      <el-table-column prop="sourceMobile" label=""></el-table-column>
      <el-table-column prop="userName" label=""></el-table-column>
      <el-table-column prop="mobile" label=""></el-table-column>
      <el-table-column prop="unit" label="/"></el-table-column>
      <el-table-column prop="number" label=""></el-table-column>
      <el-table-column prop="totalPrice" label="/"></el-table-column>
      <el-table-column prop="discountFee" label="/"></el-table-column>
      <el-table-column prop="actualFee" label="/"></el-table-column>
      <el-table-column prop="couponId" label=""></el-table-column>
      <el-table-column :formatter="formatStatus" label=""></el-table-column>
      <el-table-column :formatter="formatpayWay" label=""></el-table-column>
      <el-table-column prop="payNum" label=""></el-table-column>
      <el-table-column prop="reserveDate" label=""></el-table-column>
      <el-table-column prop="enterTime" label=""></el-table-column>
      <el-table-column prop="time" label=""></el-table-column>
      <!--<el-table-column prop="mark" label=""></el-table-column>-->
    </el-table>
    <pagination v-on:getPageData="getTablePageData" :total-num="totalNum"></pagination>
  </div>
</template>

<script>
  export default {
    name: 'OrderList',
    data() {
      return {
        dialogTableVisible: false,
        sourceName: '',
        orderId: '',
        tableData: [],
        totalNum: 0,
        gridData: []
      }
    },
    methods: {
      getTablePageData(pagerObj) {
        let params = {
          sourceName: this.sourceName,
          orderId: this.orderId,
          currentPage: pagerObj.currentPage,
          pageSize: pagerObj.pageSize
        }
        this.query(this, params)
      },
      search() {
        let params = {
          sourceName: this.sourceName,
          orderId: this.orderId
        }
        this.query(this, params)
      },
      formatStatus(row, column, cellValue) {
        let text = ''
        let status = row.payStatus
        if (status === 1) {
          text = ''
        } else if (status === 5) {
          text = ''
        } else if (status === 10) {
          text = ''
        } else if (status === 15) {
          text = ''
        } else if (status === 20) {
          text = ''
        } else if (status === 25) {
          text = ''
        } else if (status === 26) {
          text = ''
        } else if (status === 30) {
          text = ''
        } else if (status === 35) {
          text = ''
        }
        return text
      },
      formatType(row, column, cellValue) {
        let text = ''
        let type = row.type
        if (type === 1) {
          text = ''
        } else {
          text = ''
        }
        return text
      },
      formatTicketType(row, column, cellValue) {
        let text = ''
        let type = row.type
        if (type === 1) {
          text = ''
        } else {
          text = ''
        }
        return text
      },
      formatpayWay(row, column, cellValue) {
        let text = ''
        let type = row.payWay
        if (type === 1) {
          text = ''
        } else if (type === 3) {
          text = ''
        } else if (type === 5) {
          text = ''
        } else if (type === 7) {
          text = ''
        }
        return text
      },
      query(that, params) {
        console.log(params)
        that.$axios.get('/admin/order/list', {params: params}).then(function (res) {
          console.log(`ok`)
          if (res.status === 200 && res.data.code === 200) {
            that.tableData = res.data.data.tableData
            that.totalNum = res.data.data.totalNum
          } else {
            that.tableData = []
            that.totalNum = 0
          }
        }).catch((error) => {
          console.log(`err: ${error}`)
          that.tableData = []
          that.totalNum = 0
        })
      }
    },
    mounted() {
      this.query(this, {})
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-input {
    display: inline-block;
    width: 140px;
  }

  .el-select {
    width: 100px;
  }

</style>



