<template>
  <div>
    <el-row :gutter="10">
      <p class="tip"></p>
      <el-input v-model.number="orderId" @keyup.enter.native="search"
                clearable placeholder="" ref="inputRef"></el-input>
      <el-button type="primary" icon="el-icon-search" @click="search"></el-button>
      <el-button type="danger" icon="el-icon-circle-check" @click="orderPass"></el-button>
    </el-row>
    <hr>
    <table class="table" v-loading="loading">
      <tr>
        <td></td>
        <td>{{order.enterTime}}</td>
      </tr>
      <tr>
        <td></td>
        <td>{{order.reserveDate}}</td>
      </tr>
      <tr>
        <td></td>
        <td>{{order.mobile}}</td>
      </tr>
      <tr>
        <td>/</td>
        <td>{{order.totalPrice}}</td>
      </tr>
      <tr>
        <td>/</td>
        <td>{{order.actualFee}}</td>
      </tr>
      <tr>
        <td>/</td>
        <td>{{order.unit}}</td>
      </tr>
      <tr>
        <td></td>
        <td>{{order.number}}</td>
      </tr>
      <tr>
        <td>/</td>
        <td>{{order.discountFee}}</td>
      </tr>
      <tr>
        <td></td>
        <td>{{order.couponId}}</td>
      </tr>
      <tr>
        <td></td>
        <td>{{order.id}}</td>
      </tr>
      <tr>
        <td></td>
        <td>{{order.status | formatStatus}}</td>
      </tr>
      <tr>
        <td></td>
        <td>{{order.payWay | formatpayWay}}</td>
      </tr>
      <tr>
        <td></td>
        <td>{{order.payNum}}</td>
      </tr>
      <tr>
        <td></td>
        <td>{{order.type | formatType}}</td>
      </tr>
      <tr>
        <td></td>
        <td>{{order.sourceName}}</td>
      </tr>
      <tr>
        <td></td>
        <td>{{order.sourceMobile}}</td>
      </tr>
      <tr>
        <td></td>
        <td>{{order.time}}</td>
      </tr>
    </table>
  </div>
</template>

<script>
  export default {
    name: 'OrderList',
    data() {
      return {
        orderId: '',
        loading: false,
        order: {}
      }
    },
    filters: {
      formatStatus: function (payStatus) {
        let text = ''
        let status = payStatus
        if (status === 1) {
          text = ''
        } else if (status === 5) {
          text = ''
        } else if (status === 10) {
          text = ''
        } else if (status === 15) {
          text = ''
        } else if (status === 20) {
          text = ''
        } else if (status === 25) {
          text = ''
        } else if (status === 26) {
          text = ''
        } else if (status === 30) {
          text = ''
        } else if (status === 35) {
          text = ''
        }
        return text
      },
      formatType: function (type) {
        let text = ''
        if (type === 1) {
          text = ''
        } else if (type === 2) {
          text = ''
        }
        return text
      },
      formatTicketType: function (type) {
        let text = ''
        if (type === 1) {
          text = ''
        } else {
          text = ''
        }
        return text
      },
      formatpayWay: function (type) {
        let text = ''
        if (type === 1) {
          text = ''
        } else if (type === 3) {
          text = ''
        } else if (type === 5) {
          text = ''
        } else if (type === 7) {
          text = ''
        }
        return text
      }
    },
    methods: {
      search() {
        if (this.orderId) {
          let params = {orderId: this.orderId}
          this.query(this, params)
        } else {
          console.log('orderId', this.orderId)
        }
      },
      query(that, params) {
        console.log(params)
        that.loading = true
        that.$axios.get('/admin/order/id', {params: params}).then(function (res) {
          console.log(res.data)
          that.loading = false
          if (res.status === 200 && res.data.code === 200) {
            if (res.data.data && res.data.data.length > 0) {
              that.order = res.data.data[0]
            } else {
              that.order = {}
              that.$message({type: 'error', message: ''})
            }
          } else {
            that.order = {}
            that.$message({type: 'error', message: res.data.msg})
          }
        }).catch((error) => {
          console.log(`err: ${error}`)
          that.loading = false
          that.order = {}
          that.$message({type: 'error', message: error})
        })
      },
      orderPass() {
        let that = this
        if (that.order.enterTime) {
          that.$message({type: 'error', message: ''})
          return
        }
        if (that.order.id) {
          let params = {orderId: that.order.id}
          that.$axios.get('/admin/order/pass', {params: params}).then(function (res) {
            if (res.status === 200 && res.data.code === 200) {
              that.$message({type: 'success', message: ''})
              that.order = {}
              that.orderId = ''
              that.$refs.inputRef.$el.children[0].focus()
            } else {
              that.$message({type: 'error', message: res.data.msg || ''})
            }
          }).catch((error) => {
            console.log(`err: ${error}`)
            that.$message({type: 'error', message: error || ''})
          })
        } else {
          that.$refs.inputRef.$el.children[0].focus()
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-input {
    display: inline-block;
    width: 400px;
    font-size: 16px;
  }
  .tip{color: #ffaa55}
  .el-select {
    width: 100px;
  }

  .table {
    color: #444;
  }

  .table tr {
    padding: 15px;
  }

  .table td {
    width: 170px;
    padding: 8px 15px;
    border: 1px solid #ccc;
  }

  .table > tr:nth-child(odd) {
    background-color: #fafafa;
  }

</style>



